.adminpage{
    display: flex;
    flex-direction: column;
    width: 100%; 
    background-color: transparent;
}

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100px;
    padding: 4px 20px 4px 12px;
    border-bottom: 1px dotted rgb(24, 24, 40);
    overflow: hidden;
}

.header img{
    width: 100px;
    height: 80px;
    border-radius: 10px;
}

.adminpage_wrap{
    display: flex;
    flex: 1;
    justify-content: flex-start;
    align-items: flex-start;
}
@media screen and (max-width:550px) {
    .adminpage{
        width: 100%;
    }
}
